.adm-image {
  display: inline-block;
  overflow: hidden;
}
.adm-image-img {
  width: 100%;
  height: 100%;
}
.adm-image-tip {
  position: relative;
  background-color: #f3f3f3;
  height: 100%;
  min-height: 24px;
  min-width: 24px;
}
.adm-image-tip > .antd-mobile-icon {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: var(--adm-color-weak);
}
